import { Component } from '@angular/core';
import { MenuController } from '@ionic/angular';
import { ModalController } from '@ionic/angular';
import { SimpleModalPage } from '../simple-modal/simple-modal.page';
import { DomSanitizer } from '@angular/platform-browser';

declare var ExternalJSFileFunction;
@Component({
  selector: 'app-home',
  templateUrl: 'home.page.html',
  styleUrls: ['home.page.scss'],
})
export class HomePage {
  safeUrl: any;
  constructor(private menu: MenuController, private modalCtrl: ModalController, private sanitizer: DomSanitizer) {}

  CallExternalJSFileFunction() {
    ExternalJSFileFunction();
  }

  async presentModal() {
    const modal = await this.modalCtrl.create({
      component: SimpleModalPage,
      breakpoints: [0, 0.3, 0.5, 0.8],
      initialBreakpoint: 0.5
    });
    await modal.present();
  }
  ngOnInit() {
    // It could be bypassTrustResourceUrl (you would need to test)
    this.safeUrl = this.sanitizer.bypassSecurityTrustUrl('http://192.168.1.30:8080/?action=stream');
  }

}
